					:sortable='true'
					align="left"
					header-align="left"
					prop="jigoumingcheng"
					label="">
					<template #default="scope">
						{{scope.row.jigoumingcheng}}
					</template>
				</el-table-column>
				<el-table-column label="" width="300" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button class="view_btn" type="info" v-if=" btnAuth('yuyueguahao','')" @click="infoClick(scope.row.id)">
							
						</el-button>
						<el-button class="edit_btn" type="primary" @click="editClick(scope.row.id)" v-if=" btnAuth('yuyueguahao','')">
													</el-button>
						<el-button class="del_btn" type="danger" @click="delClick(scope.row.id)"  v-if="btnAuth('yuyueguahao','')">
													</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total"
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text="<"
				next-text=">"
				:hide-on-single-page="false"
				:style='{"width":"100%","padding":"0","margin":"20px 0 0","whiteSpace":"nowrap","color":"#333","fontWeight":"500"}'
				:page-sizes="[10, 20, 30, 40, 50, 100]"
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
		<payForm ref="pay" @payChange="searchClick()"></payForm>
		<!--  -->
		<el-dialog v-model="echartVisible" title="" width="70%">
            <div  id="keguashuliangEchart1" style="width:100%;height:600px;"></div>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="echartVisible=false"></el-button>
				</span>
			</template>
