	}
	// 
	.el-pagination {
		// 
		:deep(.el-pagination__total) {
			margin: 0 10px 0 0;
			color: #666;
			font-weight: 400;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 28px;
			height: 28px;
		}
		// 
		:deep(.btn-prev) {
			border: none;
			border-radius: 2px;
