<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="60%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="24">
						<el-form-item prop="touxiang"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.touxiang?true:false"
								action="file/upload"
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="form.touxiang?form.touxiang:''" 
								@change="touxiangUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="yishenggonghao">
							<el-input class="list_inp" v-model="form.yishenggonghao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yishenggonghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="yishengxingming">
							<el-input class="list_inp" v-model="form.yishengxingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yishengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="xingbie">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.xingbie?true:false"
								v-model="form.xingbie" 
								placeholder=""
								>
								<el-option v-for="(item,index) in xingbieLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zhicheng">
							<el-input class="list_inp" v-model="form.zhicheng" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.zhicheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="keshi">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.keshi?true:false"
								v-model="form.keshi" 
								placeholder=""
								>
								<el-option v-for="(item,index) in keshiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="shanzhanglingyu">
							<el-input class="list_inp" v-model="form.shanzhanglingyu" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.shanzhanglingyu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="lianxifangshi">
							<el-input class="list_inp" v-model="form.lianxifangshi" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.lianxifangshi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zuozhenshijian">
							<el-input class="list_inp" v-model="form.zuozhenshijian" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.zuozhenshijian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="guahaofei">
							<el-input class="list_inp" v-model.number="form.guahaofei" placeholder=""
                                type="number"
                                @mousewheel.native.prevent
								:readonly="!isAdd||disabledForm.guahaofei?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="keguashuliang">
							<el-input class="list_inp" v-model.number="form.keguashuliang" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.keguashuliang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="jigouzhanghao">
							<el-input class="list_inp" v-model="form.jigouzhanghao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.jigouzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="jigoumingcheng">
							<el-input class="list_inp" v-model="form.jigoumingcheng" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.jigoumingcheng?true:false" />
						</el-form-item>
					</el-col>

				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'yishengzuozhen'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        touxiang : false,
        yishenggonghao : false,
        yishengxingming : false,
        xingbie : false,
        zhicheng : false,
        keshi : false,
        shanzhanglingyu : false,
        lianxifangshi : false,
        zuozhenshijian : false,
        guahaofei : false,
        keguashuliang : false,
        jigouzhanghao : false,
        jigoumingcheng : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		touxiang: [
		],
		yishenggonghao: [
		],
		yishengxingming: [
		],
		xingbie: [
		],
		zhicheng: [
		],
		keshi: [
		],
		shanzhanglingyu: [
		],
		lianxifangshi: [
		],
		zuozhenshijian: [
		],
		guahaofei: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		keguashuliang: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		jigouzhanghao: [
		],
		jigoumingcheng: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const touxiangUploadSuccess=(e)=>{
		form.value.touxiang = e
	}
	//
	const xingbieLists = ref([])
	//
	const keshiLists = ref([])

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			touxiang: '',
			yishenggonghao: '',
			yishengxingming: '',
			xingbie: '',
			zhicheng: '',
			keshi: '',
			shanzhanglingyu: '',
			lianxifangshi: '',
			zuozhenshijian: '',
			guahaofei: '',
			keguashuliang: '',
			jigouzhanghao: '',
			jigoumingcheng: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='touxiang'){
					form.value.touxiang = row[x];
					disabledForm.value.touxiang = true;
					continue;
				}
				if(x=='yishenggonghao'){
					form.value.yishenggonghao = row[x];
					disabledForm.value.yishenggonghao = true;
					continue;
				}
				if(x=='yishengxingming'){
					form.value.yishengxingming = row[x];
					disabledForm.value.yishengxingming = true;
					continue;
				}
				if(x=='xingbie'){
					form.value.xingbie = row[x];
					disabledForm.value.xingbie = true;
					continue;
				}
				if(x=='zhicheng'){
					form.value.zhicheng = row[x];
					disabledForm.value.zhicheng = true;
					continue;
				}
				if(x=='keshi'){
					form.value.keshi = row[x];
					disabledForm.value.keshi = true;
					continue;
				}
				if(x=='shanzhanglingyu'){
					form.value.shanzhanglingyu = row[x];
					disabledForm.value.shanzhanglingyu = true;
					continue;
				}
				if(x=='lianxifangshi'){
					form.value.lianxifangshi = row[x];
					disabledForm.value.lianxifangshi = true;
					continue;
				}
				if(x=='zuozhenshijian'){
					form.value.zuozhenshijian = row[x];
					disabledForm.value.zuozhenshijian = true;
					continue;
				}
				if(x=='guahaofei'){
					form.value.guahaofei = row[x];
					disabledForm.value.guahaofei = true;
					continue;
				}
				if(x=='keguashuliang'){
					form.value.keguashuliang = row[x];
					disabledForm.value.keguashuliang = true;
					continue;
				}
				if(x=='jigouzhanghao'){
					form.value.jigouzhanghao = row[x];
					disabledForm.value.jigouzhanghao = true;
					continue;
				}
				if(x=='jigoumingcheng'){
					form.value.jigoumingcheng = row[x];
					disabledForm.value.jigoumingcheng = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('jigouzhanghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.jigouzhanghao = json.jigouzhanghao
				disabledForm.value.jigouzhanghao = true;
			}
			if(json.hasOwnProperty('jigoumingcheng')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.jigoumingcheng = json.jigoumingcheng
				disabledForm.value.jigoumingcheng = true;
			}
		})
		xingbieLists.value = ",".split(',')
		context?.$http({
			url: `option/keshi/keshi`,
			method: 'get'
		}).then(res=>{
			keshiLists.value = res.data.data
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		if(form.value.touxiang!=null) {
			form.value.touxiang = form.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
